.btnSend {
  /* display: flex; */
  border: none;
  background: rgba(255, 255, 255, 0);
  /* margin-top: 60px; */
  color: rgba(0, 0, 0, 0.6);
  font-size: 1.2em;
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  -o-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  /* margin-left: 600px; */
  /* margin-top: 20px; */
  -ms-flex-line-pack: center;
      align-content: center;
  /* margin-left: -5px; */
}

.btnSend button {
  background: rgba(255, 255, 255, 0);
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.9em;
  border: none;
  cursor: pointer;
}

.btnSend:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

.btnSend:disabled, .btnSend[disabled] {
  color: black;
}

.btnSend:enabled, .btnSend[enabled] {
  color: rgba(255, 255, 255, 0.7);
}

.letter {
  color: rgba(255, 255, 255, 0.7);
  font-weight: 900;
  position: absolute;
}



.h3Question{
  text-align: center;
  margin-top: 40px;
}


.theFormDiv{
  width:100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.theFormDiv > div{
  position: relative;
  margin: 5px;
  color: rgba(255, 255, 255, 0.7);

}

.theFormDiv > div:first-child input{
  width:370px;
}

.theFormDiv span, .radioButton{
  position: absolute;
}

.theFormDiv input{
  text-align: left;
  cursor: pointer;
}


.theForm span {
  top:0;
  left:0;
  margin-bottom:5px;
}

.radioButton{
  top:0;
  left:-25px;
  z-index: 1;
  background: red;
  width:20px;

  height: 20px;
}

.inputClass{
  position: relative;
}


